.contact {
	font-family: 'Roboto', sans-serif;
	font-weight: 200;
	display: flex;
	align-items: center;
	justify-content: flex-end;
	margin-top: 20px;
	background-color: black;
	color: white;
	font-size: 50px;
	width: 40%;
	height: 100px;
	text-align: right;
	padding-right: 15px;
}

.main-container {
	display: flex;
	justify-content: space-between;
}

.container-contact {
	min-height: calc(100vh - 275px);
	width: 25%;
	background-color: black;
	margin: 20px 0px 0px 0px;
	display: flex;
	align-items: center;
	justify-content: center;
}

.caja {
	margin-left: -300px;
	background-color: white;
	min-height: 300px;
	width: 300px;
	border: solid 3px black;
	padding: 5px;
	font-family: 'Roboto', sans-serif;
	font-size: 1em;
}

@media screen and (max-width: 768px) {
	.contact {
		width: 80%;
	}
	.main-container {
		flex-direction: column;
	}
	.caja {
		margin: 0px;
		min-width: 200px;
		max-height: 200px;
		font-size: 0.8em;
	}

	.container-contact {
		align-self: center;
		width: 80%;
	}
}
